}

.Prompt_heading__3eimX {
    font-size: 2rem;
    padding: 1rem 2rem;
}

.Prompt_header__2M7GH {
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25rem;
}

.Prompt_content__5UcjS {
    padding: 0rem 2rem;
